<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 商品订单
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-select v-model.number="query.awardType" placeholder="请选择">
                    <el-option v-for="item in AwardTypeAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="query.userName" placeholder="用户名" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Nickname" label="昵称"></el-table-column>
                <el-table-column prop="Title" label="奖品"></el-table-column>
                <el-table-column label="奖品类型">
                   <template slot-scope="scope">{{scope.row.AwardType|AwardTypeName}}</template>
                </el-table-column>
                <el-table-column label="金额">
                    <template slot-scope="scope">{{scope.row.Gold}}</template>
                </el-table-column>
                <el-table-column label="时间">
                    <template slot-scope="scope">{{scope.row.StrCreatedAt}}</template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    // import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    // import 'quill/dist/quill.bubble.css';
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';
    import { quillEditor } from 'vue-quill-editor';
    import * as Quill from 'quill';


    export default {
        name: 'SpinWinAwardList',
        data() {
            return {
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                AwardTypeAll:[
                    {name:'全部',value:0},
                    {name:'金币',value:1},
                    {name:'积分',value:2},
                ],
                query: {
                    awardType: 0,
                    userName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;
        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        components: {
            VueCropper,
            quillEditor
        },
        methods: {
            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'filmbll/getspinwinawardlist', method: 'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                console.log(row);
                this.form = row;
                this.editVisible = true;
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={

                }
                this.form = aFilm;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                if (this.idx===-1) {
                    var data = {
                        jsonData:JSON.stringify(this.form)
                    }
                    request({
                        url:'filmbll/addgoodsinfo',  method:'post',
                        data:data
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`添加成功`);
                            this.getData();
                        } else {
                            this.$message.error('添加失败：'+res.msg);
                        }
                    });
                } else {
                    var data = {
                        jsonData:JSON.stringify(this.form)
                    }
                    request({
                        url:'filmbll/updategoods',  method:'post',
                        data:data
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                            this.$set(this.tableData, this.idx, this.form);
                        } else {
                            this.$message.error('更新失败：'+res.msg);
                        }
                    });

                }
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }
</style>
